<template>
  <div class="container" :style="{height: height, width: width}">
    <div class="editer" contenteditable ref="editer">
      {{content}}
    </div>
    <div class="tool">
    
    </div>  
  </div>
</template>

<script>
import {debounce} from '@/utils/utils'
import HostParser from '@/assets/js/hostParser'

class CharacterMutation {
  
  constructor(options) {
    const {el, config, handler} = options
    this.el = el
    this.handler = handler
    this.config = Object.assign({
      childList: true,
      characterData: true,
      subtree: true
    }, config)
    this.initial()
  }

  initial() {
    const observer = this.observer = new MutationObserver(this.handler)
    this.connect()
  }

  connect() {
    const {el, config} = this
    this.observer.observe(el, config)
  }

  disconnect() {
    this.observer.disconnect()
  }

}

const getLastChild = el => {
  if(!el || !el.nodeType) {
    return el
  }
  
  const lastChild = el.lastChild
  
  if(lastChild) {
    return getLastChild(lastChild)
  } else {
    return el
  }
}

export default {
  data() {
    return {
      // 编辑器内容解析对象
      parser: null
    }
  },
  props: {
    width: {
      type: String,
      default: '580px'
    },
    height: {
      type: String,
      default: '600px'
    },
    ipStylt: {
      type: String,
      default: 'color: #1890ff'
    },
    domainStylt: {
      type: String,
      default: 'color: green'
    },
    commentsStylt: {
      type: String,
      default: 'color: #ccc'
    },
    content: {
      type: String,
      default: '# switch proxy'
    }
  },
  computed: {
    editer() {
      return this.$refs.editer
    }
  },
  mounted() {
    this.mutationObserver = new CharacterMutation({
      handler: this.textParser,
      el: this.editer
    })
    this.initialParser()
    this.textParser()
  },
  destory() {
    this.mutationObserver.disconnect()
    this.mutationObserver = null
  },
  methods: {
    // 初始化解析器
    initialParser() {
      this.parser = new HostParser({
        styles: {
          ip: 'color: #1890ff',
          domain: 'color: green',
          comments: 'color: #ccc'
        }
      })
    },
    textParser() {     
      // 插入光标描述符
      // 用文本记录当前光标位置
      document.execCommand('insertText', true, '{|}')
      
      const el = this.editer
      const content = el.innerText
      const parser = this.parser
      
      // 关闭监听
      this.mutationObserver.disconnect()
      // 解析文本
      parser.parse(content)
      // 插入新 HTML
      el.innerHTML = ''
      el.appendChild(parser.getFragment())
      // 设置光标
      this.setRange()
      // parser.linesParseData.forEach(item => {
      //   if(item.ip) {
      //     const {start, end} = item.ip
      //     var newRange = document.createRange(); // 注意，此处必须创建一个新的选区，在原来的 range 上修改无效
      //     newRange.setStart(startContainer, start);
      //     newRange.setEnd(startContainer, end + 1);
      //     console.log(startContainer)
      //     console.log(startContainer.length)
      //     console.log(end + 1)
      //     window.test=startContainer
      //     selection.removeAllRanges();
      //     selection.addRange(newRange);
      //     document.execCommand('foreColor', true, 'red')
      //   }
      // })

      // 重新开启监听
      this.mutationObserver.connect()
      // el.innerHTML = ''
      // el.appendChild(parser.getFragment())
      
      // var find = parser.findByEl(startContainer)
      // if(find) {
      //   var id = find.id
      //   var s = parser.findById(id)
      //   var textContainer = (function ymd(el) {
      //     if(el.nodeType === 3) {
      //       return el
      //     } else {
      //       return ymd(el.firstChild)
      //     }
      //   })(s.el)
      //   // 构造新的 Range
      //   var newRange = document.createRange(); // 注意，此处必须创建一个新的选区，在原来的 range 上修改无效
      //   newRange.setStart(textContainer, startOffset);
      //   //newRange.setEnd(endContainer, endOffset);

      //   // 恢复选区
      //   selection.removeAllRanges();
      //   selection.addRange(newRange); 
    },
    setRange() {
      const anchor = this.editer.querySelector('.anchor')
      if(anchor) {
        let startContainer = anchor.previousSibling || anchor.nextSibling || anchor
        let lastChild = getLastChild(startContainer)
        let flag = !!anchor.previousSibling
        let startOffset = flag ? (lastChild.length || 0) : 0
        let selection = document.getSelection()
        let range = document.createRange()
        range.setStart(lastChild, startOffset)
        selection.removeAllRanges()
        selection.addRange(range)
        anchor.remove()
      }
    },
    // 更新配置
    updateConfig() {
      
    }
  }
}
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: row;
}
.editer {
  flex-grow: 1;
  padding: 15px 20px;
  outline: 0;
  overflow-y: scroll;
  letter-spacing: .8px;
  height: 100%;
}
.tool {
  min-width: 40px;
  height: 100%;
}
.editer p {
  padding: 0 0 5px;
}
</style>