import Matcher from '@/assets/js/matcher' 

class HostParser {
  constructor(options = {}, context = document) {
    this.text = ''
    this.context = context
    this.tagName = options.tagName || 'p'
    // 锚文本
    // 匹配的文本会被替换成 <span class="anchor"></span>
    this.anchorText = options.anchorText || '{|}'
    this.style = options.styles || this._getDefaultStyle()
    this.rules = options.rules || this._getDefaultRules()
    this.initial()
    this.clean()
  }

  initial() {
    this.matcher = new Matcher(this.rules)
  }

  addRule(rule) {
    this.rules.push(rule)
    this.matcher = new Matcher(this.rules)
  }
  
  parse(text) {
    this.text = text
    this.clean()
    // 拆分行
    const lines = this._formatArray(
      text.split(/\n/)
    )

    lines.forEach(item => {
      const parseInfo = {}
      // 锚文本
      const anchorText = this.anchorText
      const findAnchorIndex = item.indexOf(anchorText)
      if(findAnchorIndex !== -1) {
        item = item.replace(anchorText, '')
        parseInfo.anchorIndex = findAnchorIndex
      }
      Object.assign(
        parseInfo,
        this.matcher.match(item)
      )
      this._replace(parseInfo)
      this._addLine(parseInfo)
    })
    
  }

  // 添加解析数据
  _addLine(options) {
    const id = `h-parser-l-${this.linesParseData.length}`
    const newOne = Object.assign({
      id,           // id
      input: '',    // 127.0.0.1 yourdomain.com # 测试环境host
      replace: '',  // <span class="ip">127.0.0.1</span> yourdomain.com # 测试环境host
      el: null,
    }, options)

    // 创建 element
    const el = this.context.createElement(this.tagName)
    el.innerHTML = newOne.replace
    el.id = id
    el.hpLine = true
    newOne.el = el
    el.style.cssText = 'margin: 0; padding: 0 0 5px 0; letter-spacing: 1px;'
    this.linesParseData.push(newOne)
  }

  _replace(parseInfo) {
    const text = parseInfo.input
    const textArr = this._string2Array(text)
    const matches = parseInfo.matches
    
    Object.keys(matches).forEach(key => {
      const capture = matches[key]
      const rule = this.rules.filter(item => item.key === key)[0]
      const setStyle = rule.setStyle || (() => '') 
      capture.forEach((info, index) => {
        const {start, end, match} = info
        const style = setStyle(index, match) || ''
        this._insertTagString({
          start, 
          end, 
          style, 
          textArr
        })
        // const startText = formatValue(textArr[start])
        // const endText = formatValue(textArr[end])
        // textArr.splice(start, 1, `<span style="display: inline-block; ${style}">${startText}`)
        // textArr.splice(end, 1, `${endText}</span>`)
        //textArr.splice(start, diff + 1, ...insert)
        // 插入的内容
        // 文本可能会有多次内容替换, 所以必须保持替换后数组长度不变, 
        //const insert = [...Array(diff).fill(''), replace(match, index)]
        //textArr.splice(start, diff + 1, ...insert)
      })
    })

    // 插入空锚
    const anchorIndex = parseInfo.anchorIndex
    if(anchorIndex || anchorIndex === 0) {
      textArr.splice(anchorIndex, 0, this._createTagString({className: 'anchor'}))
    }

    parseInfo.replace = textArr.join('')
  }

  _insertTagString({
    textArr, 
    start, 
    end, 
    style
  }) {
    if(start === end) {
      const text = this._formatValue(textArr[start])
      textArr.splice(start, 1, this._createTagString({style, text}))
    } else {
      const startText = this._formatValue(textArr[start])
      const endText = this._formatValue(textArr[end])
      textArr.splice(start, 1, this._createTagString({style, text: startText, type: 'left'}))
      textArr.splice(end, 1, this._createTagString({text: endText, type: 'right'}))
    }
  }

  _createTagString({
    style, 
    className, 
    text = '', 
    tagName = 'span', 
    // all - 创建闭合标签
    // left - 创建开始标签
    // right - 创建结束标签
    type = 'all'
  }) {
    
    const classString = className ? `class="${className}" ` : ''
    const attrString = `${classString}style="display: inline-block; ${style}"`
    
    if(type === 'all') {
      return `<${tagName} ${attrString}>${text}</${tagName}>`      
    } else if(type === 'left') {
      return `<${tagName} ${attrString}>${text}`
    } else if(type === 'right') {
      return `${text}</${tagName}>`
    }
  }

  _formatValue(value = '') {
    return value.replace(/\s/g, '&nbsp;')
  }

  _string2Array(str = '') {
    const arr = str.split('')
    return this._formatArray(arr, text => this._formatValue(text))
  }

  _formatArray(arr, process) {
    const formatArr = []
    arr.forEach(item => {
      // 数组处理
      // 去掉多余空行
      // 将空格符转义
      item && formatArr.push(
        process ? process(item) : item
      ) 
    })
    return formatArr
  }

  _getDefaultStyle() {
    return {
      ip: 'color: red',
      domain: 'color: green',
      comments: 'color: #ccc'
    }
  }

  _getDefaultRules() {
    const startRegexpStr = '^\\s*'
    const hostRegexpStr = '((?:\\d{1,3}\\.){3}\\d{1,3})(?:\\s+)([a-z]+(?:\\.[a-z])*)'
    const commentsRegexpStr = '(#+.*)'
    return [
      {
        key: 'comments',
        regexp: commentsRegexpStr,
        setStyle: () => this.style.comments
      },
      {
        key: 'host',
        test: `${startRegexpStr}${hostRegexpStr}`,
        regexp: hostRegexpStr,
        setStyle: (captureIndex, text) => {
          if(captureIndex === 0) {
            return this.style.ip
          } else if(captureIndex === 1) {
            return this.style.domain
          }
        }
      }
    ]
  }

  //
  _getDefaultLineData() {
    return {
      textArr: [],
      el: null,
    }
  }

  // 通过 element 找到对应的行数据
  findByEl(el) {
    if(!el || !el.nodeType || !el.parentNode) {
      return null
    }    
    
    const parentNode = el.parentNode
    
    if(parentNode.spLine) {
      return this.linesParseData.filter(item => item.id === parentNode.id)[0]
    } else {
      return this.findByEl(parentNode)
    }
  }

  // 通过ID找到对应的行数据
  findById(id) {
    return this.linesParseData.filter(item => item.id === id)[0]
  }

  find(text) {
    return this.linesParseData.filter(item => item.input === text)
  }

  // 获取代码片段
  getFragment() {
    const fragment = this.context.createDocumentFragment()
    this.linesParseData.forEach(item => fragment.appendChild(item.el))
    return fragment
  }

  toString() {
    const arr = []
    this.linesParseData.forEach((item, index) => arr.push(`${index > 0 ? '\n' : ''}${item.replace}`))
    return arr.join('')
  }

  clean() {
    this.linesParseData = []
  }
}

export default HostParser